.planet {
  z-index: 5;
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  left: 50%;
  top: 0;
  transform: translate(-50%, -50%);
  transform-style: preserve-3d; }
  .planet--mercury {
    width: 8px;
    height: 8px;
    background-color: brown; }
    .planet--mercury::before {
      content: "";
      display: block;
      position: absolute;
      animation-name: rotate-planet-anticlockwise;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-image: url("../img/content/mercury-bg.jpg");
      background-position: center;
      background-size: cover;
      animation-timing-function: linear;
      animation-iteration-count: infinite; }
    .planet--mercury::after {
      content: "";
      display: block;
      position: absolute;
      left: -1px;
      top: -1px;
      right: -1px;
      bottom: 0;
      border-radius: 50%;
      background-size: cover;
      background-image: linear-gradient(to bottom, #1c192b 18%, rgba(28, 25, 43, 0.9) 44%, rgba(28, 25, 43, 0.7) 64%, rgba(28, 25, 43, 0.7) 64%, rgba(255, 255, 255, 0) 100%);
      transform: rotate(5deg); }
    .planet--mercury::before {
      animation-duration: 58.64s; }
  .planet--venus {
    width: 14px;
    height: 14px;
    background-color: orange; }
    .planet--venus::before {
      content: "";
      display: block;
      position: absolute;
      animation-name: rotate-planet-anticlockwise;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-image: url("../img/content/venus-bg.jpg");
      background-position: center;
      background-size: cover;
      animation-timing-function: linear;
      animation-iteration-count: infinite; }
    .planet--venus::before {
      content: "";
      display: block;
      position: absolute;
      animation-name: rotate-planet-clockwise;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-image: url("../img/content/venus-bg.jpg");
      background-position: center;
      background-size: cover;
      animation-timing-function: linear;
      animation-iteration-count: infinite; }
    .planet--venus::after {
      content: "";
      display: block;
      position: absolute;
      left: -1px;
      top: -1px;
      right: -1px;
      bottom: 0;
      border-radius: 50%;
      background-size: cover;
